import React from 'react'
import { Row, Col, Form } from 'antd'
import { path } from 'ramda'
import styled from 'styled-components'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import { ReportTemplateSelect } from '@/services/reportTemplate'
import { DoctorSelect, useDoctors } from '@/services/pathology'
import Input from '@/components/input'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import { rules } from '@/utils/form'
import history from '@/utils/history'
import { REPORT_INPUT } from '@/constants/routes'
import { toQueryString } from '@/utils/request'

const FormItem = styled(Form.Item)``

export default function ReportInquiry() {
  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }])

  const handleConfirm = useRouteLeaveConfirm('/')

  const [form] = Form.useForm()

  const { doctorById } = useDoctors()

  const handleOk = () => {
    form.validateFields().then(values => {
      const doctor = path([values.doctorId, 'doctor'], doctorById)
      history.push(
        `${REPORT_INPUT}?${toQueryString(values)}&reviewDoctor=${doctor}`
      )
    })
  }

  useHeaderContent(
    <div>
      <Button
        color="oultine"
        className="text-white border-white bg-transparent"
        onClick={handleOk}>
        確認
      </Button>
      <Button
        color="outline"
        className="ml-3 text-white border-white bg-transparent"
        onClick={handleConfirm}>
        取消
      </Button>
    </div>
  )

  return (
    <>
      <Page style={{ height: 'calc(100vh - 203px)' }}>
        <Row justify="center">
          <Col span={20}>
            <div className="bg-white p-12 py-24 mb-8 rounded-lg shadow-lg">
              <Form
                form={form}
                layout="inline"
                className="flex justify-center"
                hideRequiredMark={true}
                colon={false}>
                <FormItem
                  className="text-lg"
                  label="審核醫師"
                  rules={[rules.required]}
                  name="doctorId">
                  <DoctorSelect
                    allowClear
                    size="large"
                    placeholder="選擇醫師"
                    style={{ width: 160 }}
                  />
                </FormItem>
                <FormItem
                  label="申請序號"
                  rules={[rules.required]}
                  name="serialNo">
                  <Input
                    size="large"
                    placeholder="輸入申請序號"
                    style={{ width: 230 }}
                  />
                </FormItem>
                <FormItem
                  rules={[rules.required]}
                  name="templateId"
                  style={{ width: 300 }}
                  label="報告格式">
                  <ReportTemplateSelect
                    bordered
                    size="large"
                    className="w-full"
                    placeholder="選擇報告模版"
                  />
                </FormItem>
              </Form>
              <div className="mt-6"></div>
            </div>
          </Col>
        </Row>
      </Page>
      {/* )} */}
    </>
  )
}
